.task-container{
    display: inline-flex;
    margin: 5px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 5px;
    background-color: white;
    opacity: 0.8;
    width: 331px;
}

.task-label{
    font-family: "PT Sans", sans-serif;
    font-size: 18px;
    flex-basis: 80%;
    min-height: 27px;
    height: auto;
    border: none;
    resize: none;
    margin: 4px 10px 4px 5px;
}

.task-label-strikethrough{
    font-family: "PT Sans", sans-serif;
    text-decoration: line-through;
    font-size: 18px;
    flex-basis: 80%;
    min-height: 27px;
    height: auto;
    border: none;
    resize: none;
    margin: 4px 10px 4px 5px;
}

.delete-button {
    right: 23px;
    margin-top: 7px;
    display: inline-list-item;
    margin-right: 10px;
}

.priority-button {
    margin-right: 10px;
    margin-top: 7px;
    display: inline-list-item;
    width: 25px;
    border-radius: 30%;
}

#low {
    background: lightgrey;
}

#medium {
    background: #ffe599;
}

#high {
    background: #d06161;
}